import { Flex, Text } from "@chakra-ui/react";
import React from "react";
import { ref, getDatabase } from "firebase/database";
import { useObject } from "react-firebase-hooks/database";
import { TOTAL_AMOUNT_TO_RAISE } from "../CONTRACT_DETAILS";

function ProgressBar1({ isLargerThan500, firebaseApp, currentPhase }) {
  const database = getDatabase(firebaseApp);
  const realtimeDBValProgressBar = "presaleProgressBar";
  const realtimeDBValTokensSold = "totalRaised";

  const [progressBarVal, progressBarVal_loading] = useObject(
    ref(database, realtimeDBValProgressBar)
  );
  const [tokensSold, tokensSold_loading] = useObject(
    ref(database, realtimeDBValTokensSold)
  );

  return (
    <>
      <Flex
        direction={"column"}
        alignItems="flex-end"
        padding={isLargerThan500 ? "1rem 2rem" : ".5rem"}
        paddingTop={"0rem"}
        width="full"
      >
        <progress
          className="progress__bar"
          value={tokensSold ? tokensSold.val() : 0}
          max={
            currentPhase === "1"
              ? TOTAL_AMOUNT_TO_RAISE.phase1
              : currentPhase === "2"
              ? Number(TOTAL_AMOUNT_TO_RAISE.phase2) +
                Number(TOTAL_AMOUNT_TO_RAISE.phase1)
              : TOTAL_AMOUNT_TO_RAISE.total
          }
          style={{ width: "100%", margin: "1rem auto" }}
        ></progress>
        <Flex gap=".35rem">
          <Text>Progress</Text>
          <Text fontWeight={"black"}>
            {!progressBarVal_loading &&
              progressBarVal &&
              `${
                Number(progressBarVal.val()) < 1 ? "<1" : progressBarVal.val()
              }%`}
          </Text>
          <Text>
            (
            {!tokensSold_loading &&
              tokensSold &&
              `$${Number(tokensSold.val()).toLocaleString()} `}
            /
            {` $${
              currentPhase === "1"
                ? TOTAL_AMOUNT_TO_RAISE.phase1
                : currentPhase === "2"
                ? Number(TOTAL_AMOUNT_TO_RAISE.phase2) +
                  Number(TOTAL_AMOUNT_TO_RAISE.phase1)
                : TOTAL_AMOUNT_TO_RAISE.total
            }`}
            )
          </Text>
        </Flex>
      </Flex>
    </>
  );
}

export default ProgressBar1;
