import {
  Button,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  Text,
  Flex,
} from "@chakra-ui/react";
import iconDropDown from "../assets/dropdown.svg";
import { BSC_COINS_SUPPORTED, ETH_COINS_SUPPORTED } from "../COINS_SUPPORTED";
import { useEffect, useState } from "react";
import { useChain } from "react-moralis";

function CoinSelector({
  dropDownLabel,
  coin,
  setCoin,
  isAuthenticated,
  isLoadingERC20Balances,
  allBalances,
  nativeBalance,
  coinBalance,
  setCoinBalance,
  currentChainIsSupported,
}) {
  /**
   * START
   * Moralis
   */
  const { chain } = useChain();
  /**
   * Moralis
   * END
   */

  let [coinsSupported, setCoinsSupported] = useState([]);
  useEffect(() => {
    if (!chain) return;
    if (chain?.chainId === "0x38") setCoinsSupported(BSC_COINS_SUPPORTED);
    else setCoinsSupported(ETH_COINS_SUPPORTED);
  }, [chain]);

  const handleChange = (e) => {
    if (!e) return;
    let coin = e.currentTarget.value + "";
    coin = coin.toUpperCase();
    setCoin(coin);
  };

  useEffect(() => {
    if (typeof currentChainIsSupported + "" === "undefined") return;
    if (!currentChainIsSupported) return;
    if (!chain) return;

    let nativeCurrencySymbol = chain?.nativeCurrency.symbol;

    if (
      isAuthenticated &&
      !isLoadingERC20Balances &&
      allBalances &&
      coin !== nativeCurrencySymbol
    ) {
      let coinSymbolToSearch = coin.toUpperCase();
      let coinAddToSearch = coinsSupported?.filter(
        (e) => e?.symbol.toUpperCase() === coin
      )[0]?.contractAddress;
      coinAddToSearch = coinAddToSearch + "";
      coinAddToSearch = coinAddToSearch.toUpperCase();
      let tempCoin = allBalances.filter((c) => {
        let symbol = c.symbol + "";
        let add = c.token_address + "";
        add = add.toUpperCase();
        return (
          symbol.toUpperCase() === coinSymbolToSearch && add === coinAddToSearch
        );
      })[0];

      tempCoin = tempCoin
        ? tempCoin.balance / Math.pow(10, tempCoin.decimals)
        : 0;
      tempCoin = Number(tempCoin);
      tempCoin = Number(tempCoin).toFixed(3);
      setCoinBalance(tempCoin);
    } else {
      setCoinBalance(nativeBalance.formatted);
    }
  }, [
    chain,
    coinsSupported,
    coin,
    allBalances,
    isAuthenticated,
    isLoadingERC20Balances,
    nativeBalance.formatted,
    currentChainIsSupported,
    setCoinBalance,
  ]);

  return (
    <Flex alignItems="center" justifyContent={"space-between"}>
      <Menu bg="rgba(0, 2, 17, 0.3)">
        <MenuButton
          as={Button}
          borderRadius="lg"
          fontSize="xs"
          height="1.5rem"
          padding=".25rem 1rem"
          marginLeft="-0.5rem"
          width="max-content"
          textAlign="left"
          rightIcon={
            <img
              src={iconDropDown}
              alt="drop down icon"
              width="10px"
              style={{ marginLeft: ".75rem" }}
            />
          }
          bg="rgba(0, 2, 17, 0)"
          _hover={{ background: "rgba(0, 2, 17, 0.5)" }}
          _active={{ background: "rgba(0, 2, 17, 0.5)" }}
          _expanded={{ background: "rgba(0, 2, 17, 0.5)" }}
        >
          <Flex gap=".5rem" alignItems="center" justifyContent="space-between">
            {dropDownLabel}{" "}
            <Flex alignItems="center" gap=".25rem">
              <img
                src={
                  coin
                    ? coinsSupported?.filter(
                        (e) => e?.symbol.toUpperCase() === coin
                      )[0]?.imgSrc
                    : ""
                }
                alt={`${coin} symbol`}
                style={{
                  borderRadius: "100%",
                  width: "16px",
                  display: "inline",
                }}
              />
              {coin}
            </Flex>
          </Flex>
        </MenuButton>
        <MenuList
          background="rgba(0, 2, 17, 0.8)"
          width="12rem"
          borderRadius="xl"
          backdropFilter="blur(10px)"
        >
          {coinsSupported?.map((c) => (
            <MenuItem
              onClick={(e) => handleChange(e)}
              _hover={{ background: "rgba(0, 2, 17, 0.5)" }}
              _focus={{ fontWeight: "bold" }}
              value={c.symbol}
              key={c.symbol}
              gap=".5rem"
            >
              <img
                src={c.imgSrc}
                alt={`${c.name} token symbol`}
                style={{ borderRadius: "100%", width: "24px" }}
              />
              <Text>{c.symbol.toUpperCase()}</Text>
              <Flex direction="column">
                <Text style={{ pointerEvents: "none" }} fontSize="0.65rem">
                  {c.name}
                </Text>
              </Flex>
            </MenuItem>
          ))}
        </MenuList>
      </Menu>
      <Text fontSize="xs">
        {isAuthenticated &&
          `Balance: ${
            isNaN(parseFloat(coinBalance))
              ? "loading..."
              : parseFloat(coinBalance)
          } ${coin !== "BNB" ? coin : "BNB"}`}
      </Text>
    </Flex>
  );
}

export default CoinSelector;
